import type { PlayerColor, BoardPosition } from '@/types/game-types';
import { ParticlePresetType, ParticlePresetConfig, PARTICLE_PRESETS, blendPresets } from './particle-presets';
import { t } from '@/i18n/index';

export type SpecialMoveType =
  | 'corner_joseki'    //  - 
  | 'great_reversal'   //  - 
  | 'total_domination' //  - 
  | 'desperate_kill'   //  - 
  | 'perfect_endgame'  //  - 
  | 'edge_control'     //  - 
  | 'chain_reaction'   //  - 
  | 'time_control';    //  - 

export interface SpecialMoveCondition {
  /**  */
  minFlipCount?: number;
  /**  */
  scoreDifference?: { min: number; max: number };
  /**  */
  positionType?: 'corner' | 'edge' | 'center';
  /**  */
  timing?: 'early' | 'mid' | 'late';
  /**  */
  special?: string;
}

export interface SpecialMoveEffect {
  /**  */
  type: SpecialMoveType;
  /**  */
  particleEffect: ParticlePresetConfig;
  /**  */
  visual: {
    screenShake: boolean;
    slowMotion: boolean;
    zoom: boolean;
    cameraMovement: boolean;
    colorFilter?: string;
  };
  /**  */
  audio: {
    soundEffect: string;
    volume: number;
    echo?: boolean;
    reverb?: boolean;
  };
  /**  */
  duration: number;
  /**  */
  display: {
    title: string;
    subtitle?: string;
    color: string;
    fontSize: number;
    animation: 'fade' | 'zoom' | 'slide' | 'explode';
  };
}

